.partnerlogos {
  margin: 1rem;
  max-height: 2.5rem;
  padding-right: 1rem; }

.main {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem; }

#partners {
  background-color: #f7fafc; }

.logo-container {
  max-width: 205px;
  margin: auto;
  height: 100%;
  padding: 1rem 1.2rem; }
  .logo-container img {
    width: 100%;
    display: block;
    margin: auto; }
  .logo-container #CSC {
    padding: 0 2rem; }
  .logo-container #NIEC {
    padding: 0 1.7rem; }
  .logo-container #SIM,
  .logo-container #LASALLE {
    width: 50%; }
  .logo-container #govtech {
    width: 90%; }
  .logo-container #NP {
    width: 85%; }
  .logo-container #NYP,
  .logo-container #SUSS,
  .logo-container #SUTD,
  .logo-container #SEAB {
    width: 80%; }
  .logo-container #SP,
  .logo-container #NTU,
  .logo-container #NUS {
    width: 75%; }
  .logo-container #TP,
  .logo-container #RP {
    width: 70%; }
