.footer {
  margin: 0 auto;
  position: relative;
  bottom: 0;
  width: 100%;
  height: auto;
  background-color: #324353;
  padding-top: 15px;
  padding-bottom: 15px;
  max-width: 1280px; }
  .footer a {
    font-family: "Montserrat", sans-serif;
