.viewer-container {
  text-align: center !important;
  padding: 1.5rem;
  height: 100%;
  justify-content: center !important;
  flex-direction: column !important;
  display: flex !important;
  border-radius: 10px; }
  .viewer-container.default {
    background-color: #f5f8fb;
    border: 2px dashed #0099cc;
    box-shadow: 0 0 0px 10px #f5f8fb; }
  .viewer-container.accept {
    background-color: #f5fbf7;
    border: 2px dashed #0099cc;
    box-shadow: 0 0 0px 10px #f5fbf7; }
  .viewer-container.warning {
    background-color: #fff9e8;
    border: 2px dashed #ffbf00;
    box-shadow: 0 0 0px 10px #fff9e8;
    color: #ffbf00; }
    .viewer-container.warning .unverified-btn {
      border: 1px solid #ffbf00;
      color: #fff;
      background-color: #ffbf00;
      padding: 7px 23px;
      border-radius: 7px;
      font-weight: 500;
      text-align: center;
      vertical-align: middle;
      min-width: 135px;
      cursor: pointer; }
      .viewer-container.warning .unverified-btn:hover {
        border: 1px solid #ffc51a;
        background-color: #ffc51a;
        box-shadow: 2px 2px 6px 0 #a7afb7;
        text-decoration: none !important;
        color: #fff; }
  .viewer-container.invalid {
    background-color: #fbebe7;
    border: 2px dashed #e46767;
    box-shadow: 0 0 0px 10px #fbebe7;
    color: #e46767; }
    .viewer-container.invalid .unverified-btn {
      border: 1px solid #e46767;
      color: #fff;
      background-color: #e46767;
      padding: 7px 23px;
      border-radius: 7px;
      font-weight: 500;
      text-align: center;
      vertical-align: middle;
      min-width: 135px;
      cursor: pointer; }
      .viewer-container.invalid .unverified-btn:hover {
        border: 1px solid #e87d7d;
        background-color: #e87d7d;
        box-shadow: 2px 2px 6px 0 #a7afb7;
        text-decoration: none !important;
        color: #fff; }

.unverified-btn {
  margin: auto; }

.image-container {
  margin-bottom: 1rem; }
  .image-container img {
    height: 110px; }

.message-container {
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
  color: #000; }
  .message-container span {
    vertical-align: middle; }

.verifications {
  margin-bottom: 2rem; }
  .verifications .messages {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 0; }

.btn {
  border: 1px solid #0099cc;
  color: #0099cc;
  background-color: #0099cc;
  padding: 7px 23px;
  border-radius: 7px;
  font-weight: 500;
  text-align: center;
  vertical-align: middle;
  min-width: 135px;
  cursor: pointer;
  background-color: #fff;
  margin: 0 auto; }
  .btn:hover {
    border: 1px solid #0099cc;
    background-color: #0099cc;
    box-shadow: 2px 2px 6px 0 #a7afb7;
    text-decoration: none !important;
    color: #fff; }

.secondary-links {
  width: 50%;
  display: flex;
  margin: 1rem auto 0 auto; }
  .secondary-links span {
    margin: auto; }
    .secondary-links span a {
      font-size: 14px; }

.text-link {
  color: #787878 !important;
  text-decoration: underline !important; }
  .text-link:hover {
    color: #324353 !important; }
