.main {
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem 3rem;
  width: 100%; }
  .main h3 {
    color: #324353;
    border-left: 6px solid #ff9933;
    padding-left: 2rem; }
  .main p {
    font-size: 1.125rem; }

.section {
  padding-top: 2rem; }

#benefits {
  background-color: #324353 !important; }
  #benefits h3 {
    color: white; }

.container {
  display: flex;
  padding: 4rem 0;
  margin: auto;
  max-width: 1080px; }
  .container div {
    text-align: center; }

.benefits {
  display: flex; }
  .benefits img {
    height: 7rem;
    padding: 0 1.5rem; }
  .benefits svg {
    height: 7rem;
    width: auto; }
  .benefits .benefit-text {
    padding-left: 2rem;
    text-align: left;
    margin: auto 0; }
    .benefits .benefit-text .benefit-header {
      color: #ff9933;
      font-weight: 600;
      padding-bottom: 0; }
    .benefits .benefit-text p {
      color: white; }

.howitworks {
  background-color: white;
  border-radius: 3px;
  border: none;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.1);
  display: block;
  max-width: 1080px;
  margin: 3rem auto;
  padding: 3rem; }
  .howitworks .hiw-container {
    max-width: 800px;
    display: block;
    margin: auto; }
    .howitworks .hiw-container div {
      display: flex;
      padding: 3rem 0; }
      .howitworks .hiw-container div p {
        text-align: left;
        padding-left: 3rem;
        margin: auto;
        white-space: pre-wrap; }
      .howitworks .hiw-container div svg {
        width: auto; }
      .howitworks .hiw-container div#onetwo svg {
        height: 145px; }
      .howitworks .hiw-container div#three svg {
        height: 105px; }
      .howitworks .hiw-container div#four i {
        margin-left: 11%; }
        .howitworks .hiw-container div#four i svg {
          height: 120px; }

@media (max-width: 600px) {
  .main {
    padding: 1rem; }
  .container {
    display: block;
    padding: 0 !important; }
  .benefits {
    padding: 2rem 0; }
    .benefits h4 {
      padding: 0; }
    .benefits p {
      padding-bottom: 2rem; }
  .howitworks {
    padding: 1rem; }
    .howitworks .hiw-container div {
      display: block;
      padding: 1rem 0;
      text-align: center; }
      .howitworks .hiw-container div p {
        padding: 1rem; }
      .howitworks .hiw-container div i {
        width: auto;
        height: 80%; } }

@media (max-width: 992px) {
  .container {
    padding: 4rem 2rem;
    display: block !important; }
  .benefits {
    padding: 2rem 0; }
    .benefits h4 {
      padding: 0; }
  .howitworks .hiw-container div {
    display: block;
    padding: 1rem 0;
    text-align: center; }
    .howitworks .hiw-container div p {
      padding-top: 1rem; }
    .howitworks .hiw-container div i {
      width: auto;
      height: 80%; }
    .howitworks .hiw-container div#four i {
      margin: 0; } }
