.innerbar {
  max-width: 1280px;
  display: flex;
  justify-content: flex-start;
  margin: 0 auto; }
  .innerbar img {
    height: 40px; }

.navbar {
  display: block;
  justify-content: flex-start;
  margin: 0 auto;
  background-color: #324353;
  padding: 1rem 2rem; }
  @media print {
    .navbar {
      display: none; } }

.nav-item {
  font-family: "Montserrat", sans-serif; }
  .nav-item a {
    font-family: "Montserrat";
    color: #c1c9d1;
    font-size: 1.15rem;
    padding: 0 0 0 3.5rem !important;
    font-weight: 500; }
    .nav-item a:hover {
      color: #fff;
      text-decoration: none; }
  .nav-item.active a {
    color: #fff;
    pointer-events: none; }

.navbar-toggler {
  display: none; }

@media (max-width: 767px) {
  .innerbar {
    display: block; }
  .nav-item {
    display: block;
    padding: .5rem 0;
    text-align: right; }
    .nav-item a {
      padding: 0 !important; }
  .navbar-toggler {
    display: inline-block;
    position: absolute;
    right: 30px;
    padding: 0.25rem 0;
    line-height: 1;
    background-color: transparent;
    border: transparent; }
    .navbar-toggler:hover {
      text-decoration: none;
      filter: brightness(110%); }
  .toggler-icon {
    height: 2em;
    width: 2em;
    color: #0099cc; } }
